.App {
  display: flex;
  flex-direction: row;
  margin-top: 3vmin;
  margin-left: 3vmin;
  margin-right: 3vmin;
  margin-bottom: 3vmin;
  max-width: 100%;
  max-height: 100%;
}
.App-container {
  display: flex;
  flex-direction: column;
  width: 13%;
}

.App-header {
  display: flex;
  flex-direction: column;
  font-family: Arial, Helvetica, sans-serif;
  font-weight:600;
  font-size: calc(2.5vmin);
  color: rgb(47, 47, 47);
  
}

.App-sub {
  display: flex;
  flex-direction: column;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 800;
  font-size: calc(2vmin);
  color: rgb(47, 47, 47);
  margin-top: 3vmin;
}

.App-abstract {
  display: flex;
  flex-direction: column;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 500;
  font-size: calc(2vmin);
  color: rgb(47, 47, 47);
  margin-top: 3vmin;
}

.App-but {
  display: flex;
  flex-direction: column;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: light;
  font-size: calc(1.8vmin);
  color: rgb(47, 47, 47);
  margin-top: auto;
}